import Vue from 'vue'
import Router from 'vue-router'
import Splash from '@/components/Splash'
import Home from '@/components/Home'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/home',
      name: 'Home',
      component: Home
    },
    {
      path: '/',
      name: 'Splash',
      component: Splash
    }
  ]
})



